import React, { useState, useEffect } from 'react';
import { createEvent, getAllEvents } from '../services/api';
import { TextField, Button, Box, Typography, Container, Paper, Input } from '@mui/material';
import AdminNavbar from "./AdminNavbar";
const EventCreate = () => {
    const [events, setEvents] = useState([]);
    const [newEvent, setNewEvent] = useState({
        eventTitle: '',
        eventDescription: '',
        registrationStartingTime: '',
        registrationEndingTime: '',
        eventScheduleTime: '',
        limit: 0,
        eventCoverPhoto: null
    });

    useEffect(() => {
        fetchEvents();
    }, []);

    const fetchEvents = async () => {
        const eventsData = await getAllEvents();
        setEvents(eventsData);
    };

    const handleFileChange = (e) => {
        setNewEvent({ ...newEvent, eventCoverPhoto: e.target.files[0] });
    };

    const handleEventSubmit = async (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('eventTitle', newEvent.eventTitle);
        formData.append('eventDescription', newEvent.eventDescription);
        formData.append('registrationStartingTime', newEvent.registrationStartingTime);
        formData.append('registrationEndingTime', newEvent.registrationEndingTime);
        formData.append('eventScheduleTime', newEvent.eventScheduleTime);
        formData.append('limit', newEvent.limit);
        formData.append('eventCoverPhoto', newEvent.eventCoverPhoto);

        await createEvent(formData);
        fetchEvents();
    };
    
   

    return (
        <div style={{ backgroundColor: '#00000', minHeight: '100vh' }} >
            <AdminNavbar/>
        <Container maxWidth="md" sx={{ mt: 4 }}>
            <Paper elevation={3} sx={{ 
                padding: 4, 
                backgroundColor: '#1a1a1a', 
                color: '#fff', 
                borderRadius: '10px', 
                border: '2px solid #00e676', 
                boxShadow: '0 0 15px #00e676' // Decreased shadow effect
            }}>
                <Typography 
                    variant="h4" 
                    align="center" 
                    gutterBottom 
                    sx={{ textShadow: '0 0 8px #00e676, 0 0 15px #00e676' }} // Decreased shadow effect
                >
                    Create a New Event
                </Typography>

                <form onSubmit={handleEventSubmit} encType="multipart/form-data">
                    <Box mb={3}>
                        <TextField
                            fullWidth
                            label="Event Title"
                            variant="outlined"
                            value={newEvent.eventTitle}
                            onChange={(e) => setNewEvent({ ...newEvent, eventTitle: e.target.value })}
                            required
                            InputLabelProps={{ style: { color: '#00e676' } }}
                            InputProps={{ style: { color: '#fff' } }}
                            sx={{ 
                                '& .MuiOutlinedInput-root': { 
                                    borderColor: '#00e676', 
                                    boxShadow: '0 0 8px #00e676' // Decreased shadow effect
                                }
                            }}
                        />
                    </Box>

                    <Box mb={3}>
                        <TextField
                            fullWidth
                            multiline
                            rows={4}
                            label="Event Description"
                            variant="outlined"
                            value={newEvent.eventDescription}
                            onChange={(e) => setNewEvent({ ...newEvent, eventDescription: e.target.value })}
                            required
                            InputLabelProps={{ style: { color: '#00e676' } }}
                            InputProps={{ style: { color: '#fff' } }}
                            sx={{ 
                                '& .MuiOutlinedInput-root': { 
                                    borderColor: '#00e676', 
                                    boxShadow: '0 0 8px #00e676' // Decreased shadow effect
                                }
                            }}
                        />
                    </Box>

                    <Box mb={3}>
                        <TextField
                            fullWidth
                            label="Registration Starting Time"
                            type="datetime-local"
                            InputLabelProps={{ shrink: true, style: { color: '#00e676' } }}
                            variant="outlined"
                            value={newEvent.registrationStartingTime}
                            onChange={(e) => setNewEvent({ ...newEvent, registrationStartingTime: e.target.value })}
                            required
                            InputProps={{ style: { color: '#fff' } }}
                            sx={{ 
                                '& .MuiOutlinedInput-root': { 
                                    borderColor: '#00e676', 
                                    boxShadow: '0 0 8px #00e676' // Decreased shadow effect
                                }
                            }}
                        />
                    </Box>

                    <Box mb={3}>
                        <TextField
                            fullWidth
                            label="Registration Ending Time"
                            type="datetime-local"
                            InputLabelProps={{ shrink: true, style: { color: '#00e676' } }}
                            variant="outlined"
                            value={newEvent.registrationEndingTime}
                            onChange={(e) => setNewEvent({ ...newEvent, registrationEndingTime: e.target.value })}
                            required
                            InputProps={{ style: { color: '#fff' } }}
                            sx={{ 
                                '& .MuiOutlinedInput-root': { 
                                    borderColor: '#00e676', 
                                    boxShadow: '0 0 8px #00e676' // Decreased shadow effect
                                }
                            }}
                        />
                    </Box>

                    <Box mb={3}>
                        <TextField
                            fullWidth
                            label="Event Schedule Time"
                            type="datetime-local"
                            InputLabelProps={{ shrink: true, style: { color: '#00e676' } }}
                            variant="outlined"
                            value={newEvent.eventScheduleTime}
                            onChange={(e) => setNewEvent({ ...newEvent, eventScheduleTime: e.target.value })}
                            required
                            InputProps={{ style: { color: '#fff' } }}
                            sx={{ 
                                '& .MuiOutlinedInput-root': { 
                                    borderColor: '#00e676', 
                                    boxShadow: '0 0 8px #00e676' // Decreased shadow effect
                                }
                            }}
                        />
                    </Box>

                    <Box mb={3}>
                        <TextField
                            fullWidth
                            label="Limit"
                            type="number"
                            variant="outlined"
                            value={newEvent.limit}
                            onChange={(e) => setNewEvent({ ...newEvent, limit: e.target.value })}
                            required
                            InputLabelProps={{ style: { color: '#00e676' } }}
                            InputProps={{ style: { color: '#fff' } }}
                            sx={{ 
                                '& .MuiOutlinedInput-root': { 
                                    borderColor: '#00e676', 
                                    boxShadow: '0 0 8px #00e676' // Decreased shadow effect
                                }
                            }}
                        />
                    </Box>

                    <Box mb={3}>
                        <Input
                            type="file"
                            onChange={handleFileChange}
                            inputProps={{ accept: 'image/*' }}
                            fullWidth
                            required
                            sx={{ color: '#00e676', boxShadow: '0 0 8px #00e676' }} // Decreased shadow effect
                        />
                    </Box>

                    <Box mb={3}>
                        <Button 
                            type="submit" 
                            variant="contained" 
                            fullWidth 
                            sx={{ 
                                backgroundColor: '#00e676', 
                                '&:hover': { backgroundColor: '#00c853' }, 
                                boxShadow: '0 0 10px #00e676' // Decreased shadow effect
                            }}
                        >
                            Create Event
                        </Button>
                    </Box>
                </form>
            </Paper>

            <Box mt={5}>
                <Typography 
                    variant="h5" 
                    align="center" 
                    gutterBottom 
                    sx={{ color: '#00e676', textShadow: '0 0 8px #00e676' }} // Decreased shadow effect
                >
                    All Events
                </Typography>
                {events.map((event) => (
                    <Paper 
                        key={event.id} 
                        elevation={2} 
                        sx={{ 
                            padding: 2, 
                            mb: 2, 
                            backgroundColor: '#1a1a1a', 
                            color: '#fff', 
                            border: '1px solid #00e676',
                            boxShadow: '0 0 10px #00e676' // Decreased shadow effect
                        }}
                    >
                        <Typography variant="h6">{event.eventTitle}</Typography>
                        <Typography variant="body2">Scheduled on: {event.eventScheduleTime}</Typography>
                    </Paper>
                ))}
            </Box>
        </Container></div>
    );
};

export default EventCreate;
