import React from 'react';
import { Link } from 'react-router-dom';

function Home() {
    // Style for the container
    const containerStyle = {
        minHeight: '100vh', // Full screen height
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        background: 'linear-gradient(90deg, black, #070638)', // Linear gradient background
        color: 'white', // Text color
        textAlign: 'center',
        position: 'relative',
    };

    // Style for the button container in the top right
    const buttonContainerStyle = {
        position: 'absolute',
        top: '20px',
        right: '20px',
        display: 'flex',
        gap: '10px',
    };

    // Style for the buttons
    const buttonStyle = {
        //backgroundColor: '#070638', // Dark background color
        color: 'white',
       // border: '1px solid black',
        padding: '0.8rem 1.5rem',
        fontSize: '1.1rem',
        cursor: 'pointer',
        borderRadius: '5px',
        textDecoration: 'none',
    };

    // Hover style for buttons
    const buttonHoverStyle = {
        backgroundColor: '#070638',
    };

    return (
        <div style={containerStyle}>
            <div style={buttonContainerStyle}>
                <Link 
                    to="/student-login" 
                    style={buttonStyle}
                    onMouseOver={(e) => e.currentTarget.style.backgroundColor = buttonHoverStyle.backgroundColor}
                    onMouseOut={(e) => e.currentTarget.style.backgroundColor = buttonStyle.backgroundColor}
                >
                    Student Login
                </Link>
                <Link 
                    to="/admin" 
                    style={buttonStyle}
                    onMouseOver={(e) => e.currentTarget.style.backgroundColor = buttonHoverStyle.backgroundColor}
                    onMouseOut={(e) => e.currentTarget.style.backgroundColor = buttonStyle.backgroundColor}
                >
                    Admin Login
                </Link>
            </div>
            <h1>Welcome to the Event Management Portal</h1>
        </div>
    );
}

export default Home;
