import React, { useEffect, useState } from 'react';
import { getAllEvents } from '../services/api'; // Assuming you have the getAllEvents function in your api.js

const ViewEvent = () => {
    const [events, setEvents] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchEvents = async () => {
            try {
                const eventData = await getAllEvents();
                setEvents(eventData);
                setLoading(false);
            } catch (err) {
                setError(err.message);
                setLoading(false);
            }
        };

        fetchEvents();
    }, []);

    if (loading) return <p>Loading events...</p>;
    if (error) return <p>Error loading events: {error}</p>;
    if (events.length === 0) return <p>No events available</p>;

    // Helper function to format date
    const formatDateTime = (dateString) => new Date(dateString).toLocaleString();

    // Inline styles for event components
    const styles = {
        eventList: {
            textAlign: 'center',
            padding: '20px',
        },
        eventCards: {
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
            gap: '20px',
        },
        eventCard: {
            border: '1px solid #ddd',
            borderRadius: '10px',
            padding: '15px',
            width: '300px',
            boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
        },
        eventImage: {
            width: '100%',
            height: '200px',
            objectFit: 'cover',
            borderRadius: '10px',
        },
        eventTitle: {
            fontSize: '20px',
            margin: '10px 0',
        },
        eventDescription: {
            fontSize: '16px',
            color: '#555',
        },
        eventDetails: {
            fontSize: '14px',
            color: '#666',
        }
    };

    return (
        <div style={styles.eventList}>
            <h2>All Events</h2>
            <div style={styles.eventCards}>
                {events.map((event) => (
                    <div style={styles.eventCard} key={event.id}>
                        <img
                            src={`http://localhost:8080${event.eventCoverPhoto}`} // Updated to handle URL correctly
                            alt={event.eventTitle}
                            style={styles.eventImage}
                        />
                        <h3 style={styles.eventTitle}>{event.eventTitle}</h3>
                        <p style={styles.eventDescription}>{event.eventDescription}</p>
                        <p style={styles.eventDetails}><strong>Registration Starts:</strong> {formatDateTime(event.registrationStartingTime)}</p>
                        <p style={styles.eventDetails}><strong>Registration Ends:</strong> {formatDateTime(event.registrationEndingTime)}</p>
                        <p style={styles.eventDetails}><strong>Scheduled:</strong> {formatDateTime(event.eventScheduleTime)}</p>
                        <p style={styles.eventDetails}><strong>Limit:</strong> {event.eventLimit} participants</p>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default ViewEvent;
